@import '~assets/styles/h3Imports';

.root {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: em(10);
  margin-top: em(10);

  @media (max-width: $max-phone-size) {
    margin-bottom: em(16);
    margin-top: 0;
  }

  & > * {
    margin-right: em(16);
    @media (max-width: $max-phone-size) {
      margin-top: em(10);
    }
  }

  :first-child {
    margin-right: em(8);
  }

  :last-child {
    margin-right: 0;
  }
}

.filter {
  align-items: center;
  display: flex;
}

.filter-value {
  color: var(--accent-interactive);
}
